.wrapper {
  width: 100%;
  border-radius: 4px;
  background: #fff;
  border: 1px solid #cbcbcb;
  transition: all 0.3s;
}

.textField {
  width: 100%;
  border: 0;
  border-radius: 4px;
  padding: 0.1rem 0.5rem;
  box-sizing: border-box;
  height: 100%;
}

.textField:focus {
  outline: none;
}
